<template>
    <div class="header-menu">
        <div id="mySidenav" class="sidenav">
            <a href="#" class="closebtn" @click="closeNav()">&times;</a>
            <ul class="nav flex-column">
                <li class="nav-item " v-for="page in topPages" :key="page.title">
                    <a class="nav-link link-size link-color" :class="isDropdown(page)" href="#">
                        {{ page.title }}
                    </a>
                </li>
            </ul>
        </div>
        <button class="navbar-toggler position-fixed" @click="openNav()">
            <i class="fas fa-bars toogler-icon"></i>
        </button>
    </div>
</template>
<script>
export default {
    data() {
        return {
            topPages: [
                {
                    title: 'Dojazd do szlaku, do Cassino, parkingi',
                    subpages: []
                },
                {
                    title: 'Przewodnicy współpracujący przy ścieżce',
                    subpages: []
                },
                {
                    title: 'Hotele wspierające projekt',
                    subpages: []
                },
                {
                    title: 'Informacje dla podróżnych (telefony, adresy, strony www)',
                    subpages: []
                },
                {
                    title: 'Ważne miejsca w Cassino',
                    subpages: [
                        {
                            title: 'Pomnik Andersa'
                        },
                        {
                            title: 'Cmentarz Polski'
                        },
                        {
                            title: 'Klasztor na Monte Cassino'
                        }
                    ]
                },
            ]
        }
    },
    methods: {
        isDropdown: function (page) {
            return page.subpages.length === 0 ? '' : 'dropdown';
        },
        openNav: function () {
            document.getElementById("mySidenav").style.width = "250px";
            document.getElementById("main").style.marginRight = "250px";
        },
        closeNav: function () {
            document.getElementById("mySidenav").style.width = "0";
            document.getElementById("main").style.marginRight = "0";
        }
    }
}

</script>
<style lang="scss" scoped>

$tooglerColor: #cc7761;
$iconSize: 20px;
$topMenuBgColor: #364b51;

.navbar-toggler {
  color: $tooglerColor;
  border-color: $tooglerColor;
  z-index: 1;
  top: 1rem;
  right: 1rem;
}

.toogler-icon {
  color: $tooglerColor;
  font-size: $iconSize;
  z-index: 1;
}

.navbar-expand-lg {
  background-color: $topMenuBgColor;
}

.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 10;
  top: 0;
  right: 0;
  background-color: #1d2331;
  overflow-x: hidden;
  padding-top: 60px;
  transition: 0.5s;
}

.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  color: #818181;
  display: block;
  transition: 0.3s;
  text-align: left;
}

.sidenav a:hover {
  color: $tooglerColor;
}

.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 40%;
  font-size: 36px;
}

#main {
  transition: margin-left .5s;
  padding: 20px;
}

@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px;
  }

  .sidenav a {
    font-size: 18px;
  }
}
</style>